import { Component, OnInit } from "@angular/core";
import { ComponentsService } from "./../components.service";
import { Observable, Subscription } from "rxjs";
import { startWith, map } from "rxjs/operators";
import {
  FormArray,
  ReactiveFormsModule,
  FormControl,
  FormsModule
} from "@angular/forms";
import { MatAutocompleteModule } from "@angular/material/autocomplete";

@Component({
  selector: "app-adjust-stock",
  templateUrl: "./adjust-stock.component.html",
  styleUrls: ["./adjust-stock.component.css"]
})
export class AdjustStockComponent implements OnInit {
  myControl = new FormControl();
  components: string[] = [];
  partData;
  filteredComponens: Observable<string[]>;
  componentsSubscription: Subscription;
  partSubscription: Subscription;

  constructor(private componentsService: ComponentsService) {
    this.components = this.componentsService.components;
    this.componentsSubscription = this.componentsService
      .getComponents()
      .subscribe(components => {
        this.components = components;
        this.filteredComponens = this.myControl.valueChanges.pipe(
          startWith(""),
          map(value => this._filter(value))
        );
      });

    this.partSubscription = this.componentsService
      .getPartInfo()
      .subscribe(data => {
        this.partData = data;
        console.warn(data);
      });
  }

  selectedComponent(PN: string) {
    this.componentsService.setSelectedPart(PN);
    console.info(PN);
  }
  ngOnInit() {
    this.filteredComponens = this.myControl.valueChanges.pipe(
      startWith(""),
      map(value => this._filter(value))
    );
    this.componentsService.getPartInfo().subscribe();
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.components.filter(option =>
      option.toLowerCase().includes(filterValue)
    );
  }
}
